"use client";

import React, { useState, useEffect, useRef } from "react";
import { motion } from "framer-motion";
import {
  Play,
  Pause,
  RotateCcw,
  Coffee,
  Brain,
  Armchair,
  Timer,
  History,
  Maximize2,
  Minimize2,
  CheckCircle2,
} from "lucide-react";
import { usePomodoro } from "@/lib/db/usePomodoro";
import { format } from "date-fns";

// --- CONFIG ---
const MODES = {
  work: {
    id: "work",
    label: "Deep Focus",
    minutes: 25,
    color: "text-indigo-500",
    bg: "bg-indigo-500",
    lightBg: "bg-indigo-50",
    border: "border-indigo-200",
    gradient: "from-indigo-500 to-purple-500",
  },
  short: {
    id: "short",
    label: "Short Break",
    minutes: 5,
    color: "text-teal-500",
    bg: "bg-teal-500",
    lightBg: "bg-teal-50",
    border: "border-teal-200",
    gradient: "from-teal-400 to-emerald-500",
  },
  long: {
    id: "long",
    label: "Long Break",
    minutes: 15,
    color: "text-blue-500",
    bg: "bg-blue-500",
    lightBg: "bg-blue-50",
    border: "border-blue-200",
    gradient: "from-blue-400 to-cyan-500",
  },
};

export default function PomodoroPage() {
  const { addSession, todaySessions } = usePomodoro();

  // State
  const [mode, setMode] = useState<"work" | "short" | "long">("work");
  const [timeLeft, setTimeLeft] = useState(MODES.work.minutes * 60);
  const [isActive, setIsActive] = useState(false);
  const [isZenMode, setIsZenMode] = useState(false);

  const audioRef = useRef<HTMLAudioElement | null>(null);
  const timerInterval = useRef<NodeJS.Timeout | null>(null);

  // --- 1. INITIALIZATION & PERSISTENCE ---
  useEffect(() => {
    audioRef.current = new Audio(
      "https://actions.google.com/sounds/v1/alarms/beep_short.ogg"
    );

    // Check LocalStorage for running timer on mount
    const savedTarget = localStorage.getItem("pomodoro_target_time");
    const savedMode = localStorage.getItem("pomodoro_mode") as
      | "work"
      | "short"
      | "long";

    if (savedTarget && savedMode) {
      const targetTime = parseInt(savedTarget, 10);
      const now = Date.now();
      const diff = Math.ceil((targetTime - now) / 1000);

      if (diff > 0) {
        // Resume Timer
        setMode(savedMode);
        setTimeLeft(diff);
        setIsActive(true);
      } else {
        // Timer finished while away
        localStorage.removeItem("pomodoro_target_time");
        localStorage.removeItem("pomodoro_mode");
        setMode(savedMode);
        setTimeLeft(0); // Show finished state
      }
    }
  }, []);

  // --- 2. TIMER LOGIC (The Heart) ---
  useEffect(() => {
    if (isActive) {
      // If we just started (no target in LS), set it
      if (!localStorage.getItem("pomodoro_target_time")) {
        const targetTime = Date.now() + timeLeft * 1000;
        localStorage.setItem("pomodoro_target_time", targetTime.toString());
        localStorage.setItem("pomodoro_mode", mode);
      }

      timerInterval.current = setInterval(() => {
        const targetTime = parseInt(
          localStorage.getItem("pomodoro_target_time") || "0",
          10
        );
        const now = Date.now();
        const diff = Math.ceil((targetTime - now) / 1000);

        if (diff <= 0) {
          handleTimerComplete();
        } else {
          setTimeLeft(diff);
          document.title = `${formatTime(diff)} - ${MODES[mode].label}`;
        }
      }, 1000);
    } else {
      // Paused
      if (timerInterval.current) clearInterval(timerInterval.current);
      localStorage.removeItem("pomodoro_target_time"); // Clear persistence on pause
      document.title = "TaskGlyph - Focus";
    }

    return () => {
      if (timerInterval.current) clearInterval(timerInterval.current);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isActive, mode]);

  const handleTimerComplete = () => {
    setIsActive(false);
    setTimeLeft(0);
    localStorage.removeItem("pomodoro_target_time");

    // Play Sound
    audioRef.current?.play().catch((e) => console.log("Audio play failed", e));

    // Save to DB (Offline/Online handled by hook)
    if (mode === "work") {
      addSession(MODES[mode].minutes, "work");
      // Ask for break?
      if (confirm("Focus session complete! Start a short break?")) {
        switchMode("short");
        // Auto start break (optional, keeping it manual for control)
        // toggleTimer();
      }
    } else {
      addSession(MODES[mode].minutes, "break");
      if (confirm("Break over! Ready to focus?")) {
        switchMode("work");
      }
    }
  };

  const toggleTimer = () => {
    if (!isActive && timeLeft === 0) {
      // Restart if 0
      setTimeLeft(MODES[mode].minutes * 60);
    }
    setIsActive(!isActive);
  };

  const resetTimer = () => {
    setIsActive(false);
    localStorage.removeItem("pomodoro_target_time");
    setTimeLeft(MODES[mode].minutes * 60);
  };

  const switchMode = (newMode: "work" | "short" | "long") => {
    setMode(newMode);
    setIsActive(false);
    localStorage.removeItem("pomodoro_target_time");
    localStorage.setItem("pomodoro_mode", newMode);
    setTimeLeft(MODES[newMode].minutes * 60);
  };

  const formatTime = (seconds: number) => {
    const m = Math.floor(seconds / 60);
    const s = seconds % 60;
    return `${m}:${s < 10 ? "0" : ""}${s}`;
  };

  // --- VISUALS ---
  const radius = 120;
  const circumference = 2 * Math.PI * radius;
  const totalTime = MODES[mode].minutes * 60;
  const progress = ((totalTime - timeLeft) / totalTime) * circumference;

  // Stats
  const totalFocusMinutes =
    todaySessions
      ?.filter((s) => s.type === "work")
      .reduce((acc, curr) => acc + curr.durationMinutes, 0) || 0;
  const sessionsCount =
    todaySessions?.filter((s) => s.type === "work").length || 0;

  return (
    <div
      className={`min-h-screen transition-colors duration-700 flex items-center justify-center relative overflow-hidden ${
        isZenMode ? "bg-gray-900" : "bg-[#F3F4F6]"
      }`}
    >
      {/* Dynamic Ambient Background */}
      {!isZenMode && (
        <>
          <div
            className={`absolute top-[-10%] left-[-10%] w-[500px] h-[500px] rounded-full mix-blend-multiply filter blur-[100px] opacity-30 animate-blob ${MODES[mode].bg}`}
          ></div>
          <div
            className={`absolute bottom-[-10%] right-[-10%] w-[500px] h-[500px] rounded-full mix-blend-multiply filter blur-[100px] opacity-30 animate-blob animation-delay-2000 ${MODES[mode].bg}`}
          ></div>
        </>
      )}

      <div
        className={`max-w-6xl w-full grid grid-cols-1 lg:grid-cols-3 gap-8 z-10 p-6 transition-all duration-500 ${
          isZenMode ? "lg:grid-cols-1" : ""
        }`}
      >
        {/* --- MAIN TIMER CARD --- */}
        <motion.div
          layout
          className={`lg:col-span-2 bg-white/80 backdrop-blur-2xl rounded-[40px] shadow-2xl p-10 flex flex-col items-center justify-center relative border border-white/50 transition-all duration-500 ${
            isZenMode
              ? "lg:col-span-1 max-w-2xl mx-auto bg-black/40 border-white/10 text-white"
              : ""
          }`}
        >
          {/* Zen Toggle */}
          <button
            onClick={() => setIsZenMode(!isZenMode)}
            className={`absolute top-6 right-6 p-3 rounded-full transition-colors ${
              isZenMode
                ? "bg-white/10 hover:bg-white/20 text-white"
                : "bg-gray-100 hover:bg-gray-200 text-gray-500"
            }`}
            title="Zen Mode"
          >
            {isZenMode ? <Minimize2 size={20} /> : <Maximize2 size={20} />}
          </button>

          {/* Mode Switcher */}
          <div
            className={`flex gap-2 p-1.5 rounded-2xl mb-10 transition-colors ${
              isZenMode ? "bg-white/10" : "bg-gray-100"
            }`}
          >
            {(Object.keys(MODES) as Array<keyof typeof MODES>).map((m) => (
              <button
                key={m}
                onClick={() => switchMode(m)}
                className={`flex items-center gap-2 px-6 py-3 rounded-xl text-sm font-bold transition-all duration-300 ${
                  mode === m
                    ? `bg-white shadow-md ${MODES[m].color} scale-105`
                    : `text-gray-400 hover:text-gray-600 ${
                        isZenMode ? "hover:text-gray-300" : ""
                      }`
                }`}
              >
                {m === "work" && <Brain size={18} />}
                {m === "short" && <Coffee size={18} />}
                {m === "long" && <Armchair size={18} />}
                <span className="hidden sm:inline">{MODES[m].label}</span>
              </button>
            ))}
          </div>

          {/* The Clock */}
          <div
            className="relative mb-12 group cursor-pointer"
            onClick={toggleTimer}
          >
            {/* Outer Glow */}
            <div
              className={`absolute inset-0 rounded-full blur-3xl opacity-20 bg-gradient-to-tr ${
                MODES[mode].gradient
              } transition-opacity duration-700 ${
                isActive ? "opacity-40" : ""
              }`}
            ></div>

            {/* SVG Ring */}
            <svg className="w-80 h-80 transform -rotate-90 drop-shadow-2xl">
              <circle
                cx="160"
                cy="160"
                r={radius}
                stroke="currentColor"
                strokeWidth="8"
                fill="transparent"
                className={isZenMode ? "text-white/10" : "text-gray-100"}
              />
              <circle
                cx="160"
                cy="160"
                r={radius}
                stroke="url(#gradient)"
                strokeWidth="8"
                fill="transparent"
                strokeDasharray={circumference}
                strokeDashoffset={-progress}
                strokeLinecap="round"
                className="transition-all duration-1000 ease-linear"
              />
              <defs>
                <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%">
                  <stop
                    offset="0%"
                    stopColor={
                      mode === "work"
                        ? "#6366f1"
                        : mode === "short"
                        ? "#14b8a6"
                        : "#3b82f6"
                    }
                  />
                  <stop
                    offset="100%"
                    stopColor={
                      mode === "work"
                        ? "#a855f7"
                        : mode === "short"
                        ? "#10b981"
                        : "#06b6d4"
                    }
                  />
                </linearGradient>
              </defs>
            </svg>

            {/* Digital Time */}
            <div className="absolute inset-0 flex flex-col items-center justify-center">
              <span
                className={`text-8xl font-bold tracking-tighter font-mono tabular-nums transition-colors duration-500 ${
                  isZenMode ? "text-white" : "text-gray-800"
                }`}
              >
                {formatTime(timeLeft)}
              </span>
              <span
                className={`text-sm font-bold uppercase tracking-[0.2em] mt-4 ${
                  isZenMode ? "text-white/50" : "text-gray-400"
                }`}
              >
                {isActive ? "Focusing..." : "Paused"}
              </span>
            </div>
          </div>

          {/* Controls */}
          <div className="flex items-center gap-6">
            <button
              onClick={toggleTimer}
              className={`w-20 h-20 rounded-[2rem] flex items-center justify-center text-white shadow-xl hover:shadow-2xl hover:scale-105 active:scale-95 transition-all bg-gradient-to-tr ${MODES[mode].gradient}`}
            >
              {isActive ? (
                <Pause size={32} fill="currentColor" />
              ) : (
                <Play size={32} fill="currentColor" className="ml-1" />
              )}
            </button>

            <button
              onClick={resetTimer}
              className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all hover:rotate-180 duration-500 ${
                isZenMode
                  ? "bg-white/10 text-white hover:bg-white/20"
                  : "bg-white text-gray-400 hover:text-gray-600 shadow-md hover:shadow-lg"
              }`}
            >
              <RotateCcw size={22} />
            </button>
          </div>
        </motion.div>

        {/* --- RIGHT: STATS PANEL (Hidden in Zen Mode) --- */}
        {!isZenMode && (
          <div className="flex flex-col gap-6">
            {/* Daily Goal */}
            <div className="bg-white/80 backdrop-blur-xl rounded-[30px] shadow-lg border border-white/50 p-8">
              <h3 className="text-lg font-bold text-gray-800 mb-6 flex items-center gap-2">
                <Timer size={20} className="text-indigo-500" /> Today&apos;s
                Progress
              </h3>
              <div className="space-y-6">
                <div className="bg-indigo-50/50 p-5 rounded-2xl border border-indigo-100">
                  <div className="flex justify-between items-end mb-2">
                    <span className="text-3xl font-bold text-indigo-600">
                      {sessionsCount}
                    </span>
                    <span className="text-xs font-bold text-indigo-400 uppercase mb-1">
                      Sessions
                    </span>
                  </div>
                  <div className="w-full h-2 bg-indigo-100 rounded-full overflow-hidden">
                    <div
                      className="h-full bg-indigo-500 rounded-full"
                      style={{ width: `${Math.min(sessionsCount * 10, 100)}%` }}
                    ></div>
                  </div>
                </div>

                <div className="bg-teal-50/50 p-5 rounded-2xl border border-teal-100">
                  <div className="flex justify-between items-end mb-2">
                    <span className="text-3xl font-bold text-teal-600">
                      {(totalFocusMinutes / 60).toFixed(1)}
                    </span>
                    <span className="text-xs font-bold text-teal-400 uppercase mb-1">
                      Hours
                    </span>
                  </div>
                  <div className="w-full h-2 bg-teal-100 rounded-full overflow-hidden">
                    <div
                      className="h-full bg-teal-500 rounded-full"
                      style={{
                        width: `${Math.min(
                          (totalFocusMinutes / 60) * 20,
                          100
                        )}%`,
                      }}
                    ></div>
                  </div>
                </div>
              </div>
            </div>

            {/* History */}
            <div className="flex-1 bg-white/80 backdrop-blur-xl rounded-[30px] shadow-lg border border-white/50 p-8 flex flex-col overflow-hidden">
              <h3 className="text-lg font-bold text-gray-800 mb-4 flex items-center gap-2">
                <History size={20} className="text-gray-400" /> Recent Activity
              </h3>

              <div className="flex-1 overflow-y-auto custom-scrollbar pr-2 -mr-2 space-y-3">
                {todaySessions && todaySessions.length > 0 ? (
                  [...todaySessions].reverse().map((session) => (
                    <div
                      key={session.id}
                      className="flex items-center justify-between p-4 bg-white rounded-2xl border border-gray-50 shadow-sm hover:shadow-md transition-shadow"
                    >
                      <div className="flex items-center gap-4">
                        <div
                          className={`p-2 rounded-full ${
                            session.type === "work"
                              ? "bg-indigo-100 text-indigo-600"
                              : "bg-teal-100 text-teal-600"
                          }`}
                        >
                          {session.type === "work" ? (
                            <Brain size={16} />
                          ) : (
                            <Coffee size={16} />
                          )}
                        </div>
                        <div>
                          <p className="text-sm font-bold text-gray-700">
                            {session.type === "work"
                              ? "Focus Session"
                              : "Break Time"}
                          </p>
                          <p className="text-xs text-gray-400 font-medium">
                            {format(new Date(session.completedAt), "h:mm a")}
                          </p>
                        </div>
                      </div>
                      <div className="flex items-center gap-2 text-gray-600 font-bold text-sm">
                        {session.durationMinutes}m
                        <CheckCircle2 size={16} className="text-green-500" />
                      </div>
                    </div>
                  ))
                ) : (
                  <div className="h-full flex flex-col items-center justify-center text-gray-300 text-sm py-10">
                    <Timer size={48} className="mb-4 opacity-20" />
                    <p>No sessions yet today.</p>
                    <p className="text-xs mt-1">Time to focus!</p>
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
